import React from "react";
import Nav from "./Nav.jsx";
import Content from "./Content.jsx";
import "../styles/Main.css";




const navList = [
 { name: "Команда", isActive: true },
  { name: "Еда", isActive: false },
  { name: "Напитки", isActive: false },
  { name: "События", isActive: false },
   // { name: "Party time", isActive: false },
  { name: "Как нас найти", isActive: false }

];

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: navList[0],
      staticPos: true //change to false for scroll effects
    };
  }

  // componentDidMount() {
  //   document.addEventListener("scroll", this.onScrollDown.bind(this))
  // }

  // onScrollDown() {
  //    let hi = window.innerHeight;
  //   if ( window.pageYOffset >= hi && this.state.staticPos === false ) {
  //       this.setStatic();
  //       this.tellHero();
  //     }
  // }

  // setStatic() {
  //   this.setState({
  //     staticPos: true
  //   })
  // }

  // tellHero() {
  //   this.props.setHeroMargin();
  // } 

  handleEvents = item => {
    this.setState({ activeTab: item });
    document.documentElement.scrollTop = window.innerHeight;
  }

  handleClick = item => {
    this.setState({ activeTab: item });
  };

  render() {
    return (
      <div className={"main " + (this.state.staticPos ? "static" : "")} >
        <Nav
          navList={navList}
          activeTab={this.state.activeTab}
          changeTab={this.handleClick}
        />
        <Content changeTab={this.handleEvents} activeTab={this.state.activeTab} />
        {/*{this.state.activeTab.name === "Party time" ? <Footer /> : null}*/}
      </div>
    );
  }
}

export default Main;



