import React from 'react';
import '../styles/Tab.css';

class Tab extends React.Component {
  render() {
    return  <li className="tab" onClick={this.props.handleClick}>
              <button className={"tab__button " + (this.props.isActive ? "tab__button--active" : "")}  >{this.props.item.name}</button>
            </li>
  }
}

export default Tab;


