import React from 'react';
import Event from './Event.jsx';
import '../styles/Events.css';


const eventList = [
{day: 'Вторник', time: '19:00', title: 'День настолок', about: '<p>Раз в две недели собираемся и играем в настольные игры.</p><p>В нашей коллекции около 100500 настолок всех сортов и расцветок.</p>'},
{day: 'Среда', time: '19:00', title: 'Митап', about: '<p>Каждую последнюю среду месяца в коворкинге проходят встречи по фронтенду с докладами и презентациями. А после митапов мы устраиваем афтепати в нашем баре.</p><p> Если вы хотите выступить с докладом, <a href="mailto:hi@spb-frontend.ru">пишите организаторам</a>.</p>'},
{day: 'Четверг', time: '9:30', title: 'Фронтенд-завтрак', about: '<p>Раз в две недели завтракаем в компании Киану, Ждуна и Бена&nbsp;Аффлека.</p><p> Иногда на завтраки приходят фронтендеры, но это неточно.</p>'},
{day: 'Пятница', time: '19:00', title: 'Party hard', about: '<p>Этот день.</p> <p>Выпускаем гуся, пэррота и кракена.</p><p>Слушаем <a href="https://spb-frontend.ru/podcast">подкаст</a>.</p><p>Праздник на нашей улице.</p>'}
]



class Events extends React.Component {

    constructor(props) {
      super(props);
      this.state ={
        party: false
      }
    }

    partyBgChange() {
      this.setState((prevSt) => ({
        party: !prevSt.party
      }))

    }

    render() {
      return    <div className={"events " + (this.state.party ? "events--party" : "")}>

        {eventList.map((item, i )=> (

          <Event key={`events_${i}`} when={item} handleBgParty={() => this.partyBgChange()}/>
        ))}
                </div>
    }
  }

  export default Events;



