import React from 'react';
import "../styles/Team.css";
import Interior from "../images/team.jpg"


class Team extends React.Component {


    render() {
      return    (
        <div className="team">
          <div className="team__inner">
            <img src={Interior} alt="interior" className="team__interior"/>
            <div className="team__intro">
            <h3 className="team__title">
              Добро пожаловать
            </h3>
            <div className="team__text">
              <p>Рады приветствовать вас в стенах нашего тёплого лампового буфета.</p>
              <p>Один из наших залов занимает коворкинг, во втором находится&nbsp;бар.</p>
              <p>Основная идея коворкига &mdash; создать площадку, которая бы объединила питерское сообщество фронтенд-разработчиков.</p>
              <p>Впрочем, мы всегда рады видеть у себя всех, кто любит работать,&nbsp;есть, пить и неравнодушен к попугаям.</p>
              <p>Коворкинг оснащён всем необходимым для максимально эффективной работы &mdash; широкие столы, мягкие стулья, розетки, регулируемое освещение, большой выбор чая, кофе, фруктов и печенек.</p>
              <p>Кроме того в зоне коворкинга есть проектор и сцена, и мы устраиваем митапы, образовательные и музыкальные встречи. Подробнее - <span className="team__link" onClick={() => this.props.toEvents()}>События</span></p>
              <p>В помещении бара больше чилаута и тропической эстетики. В меню преобладает азиатская кухня и блюда из свежих овощей и фруктов. Из классики &mdash; бургеры, пицца, шаверма и рёбрышки.</p>
              <p>А летом мы открываем крышу и устраиваем барбекю-вечеринки под открытым небом, с бассейном, фейерверками и живой музыкой.</p>
              <p>Ждём вас ежедневно с 10:00.</p>
              <p>Ъоьь.</p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

  export default Team;



