import React from 'react';
import FoodBlock from './FoodBlock.jsx';
import '../styles/Headings.css';
import '../styles/Drinks.css';
import Shuffle from "../libs/shuffle.js"

const drinkList = [
[
  {name: 'Чай (сорта уточняйте у Ждуна)', price: 2.5, block: 'Кофе, чай'},
  {name: 'Эспрессо', price: 1.5, block: 'Кофе, чай'},
  {name: 'Капучино', price: 2.5, block: 'Кофе, чай'},
  {name: 'Латте', price: 2.5, block: 'Кофе, чай'},
  {name: 'Раф', price: 2.5, block: 'Кофе, чай'}

],
 [
    {name: 'Безалкогольный сидр', price: 2.5, block: 'Софт'},
    {name: 'Безалкогольная "Кровавая Мери"', price: 2.5, block: 'Софт'},
    {name: 'Домашний лимонад', price: 2.5, block: 'Софт'},
    {name: 'Смузи', price: 2.5, block: 'Софт'},
    {name: 'Кола', price: 2.5, block: 'Софт'},
    {name: 'Свежевыжатый сок', price: 2.5, block: 'Софт'},
    {name: 'Куриный сок с мякотью', price: 2.5, block: 'Софт'},
    {name: 'Кумыс', price: 2.5, block: 'Софт'},
  
  ],
[
  {name: 'Сидр', price: 3.5, block: 'Хард'}, 
  {name: 'Стаут', price: 3.5, block: 'Хард'},
  {name: 'Ипа', price: 3.5, block: 'Хард'}, 
  {name: 'Егерьмайстер', price: 3.5, block: 'Хард'},
  {name: 'Джемесон', price: 3.5, block: 'Хард'},
  {name: 'Виски с колой', price: 3.5, block: 'Хард'}
]]



class Drinks extends React.Component {

    state = {
    plants: [1,2,3,4,5,6]
  }

componentWillMount() {
  this.setState({
    plants: Shuffle.make(this.state.plants)
})}

    render() {
      return (    
        <div className="drinks">
          {drinkList.map((item, i )=> (
            <FoodBlock key={`drinks_${i}`} styleClass={"foodblock--" + this.state.plants[i]} foodList={item}/>
          ))}

          <div className="foodblock food__note">
            <p>
              <span>*</span> Все цены указаны в пэрроткоинах.
            </p>
            <p>1PRT = 0.001ETH</p>
          </div>
                
        </div>
      )
    }
  }
  
  export default Drinks;


