import React from "react";
import Parrot from "./Parrot.jsx";
import "../styles/Hero.css";

class Hero extends React.Component {
  render() {
    return (
      <div className={"hero " + (this.props.zeroMargin ? "zero" : "")}>
        <Parrot />
      </div>
    );
  }
}

export default Hero;



