import React from 'react';
import Hero from '../components/Hero.jsx';
import Main from '../components/Main.jsx';
import Footer from '../components/Footer.jsx';



 
class Wrap extends React.Component {
  constructor(props) {
    super();
    this.state = {
      heroZeroMargin: false
    }
  }

  setHeroMargin() {
    this.setState({
      heroZeroMargin: true
    })
  }

  render() {
    return <div>
            <Hero zeroMargin={this.state.heroZeroMargin}/><Main setHeroMargin={() => {this.setHeroMargin()}}/><Footer/>
          </div>
  }
}




export default Wrap;


