import React from 'react';
import '../styles/FoodBlock.css';


class FoodBlock extends React.Component {


    render() {
      return    <div className={"foodblock " + this.props.styleClass }>
                    <h3 className="foodblock__title h3">                   {this.props.foodList[0].block}
                    </h3>
                    <ul className="foodblock__list">
                        {this.props.foodList.map( (item, i) =>
                            <li key={i} className="foodblock__item">
                                <span className="foodblock__dish">
                                    {item.name}
                                </span>
                                <span className="foodblock__price">
                                    {item.price}
                                </span>
                            </li>
                        )}
                    </ul>
                    

                </div>
    }
  }
  
  export default FoodBlock;


