import React from "react";
import "../styles/Parrot.css";
import Logo from "../images/parrot-logo_2.9.svg";

class Parrot extends React.Component {
  render() {
    return (
      <div className="parrot">
        <img alt="" className="parrot__img" src={Logo} />
      </div>
    );
  }
}

export default Parrot;



