import React from 'react';
import '../styles/Event.css';

class Event extends React.Component {

  constructor(props) {
    super(props);
    this.eventData = this.props.when;
  }

  handleBgParty(e) {
    e.stopPropagation();
   if ( this.props.when.day === "Пятница" ) {
      this.props.handleBgParty()
    } else return;
  }


 pasteEvent() {
  return {__html: this.eventData.about};
}



    render() {
      return    <div onMouseEnter={(e) => this.handleBgParty(e)} onMouseLeave={(e) => this.handleBgParty(e)} className={"event " + (this.props.when.day === "Пятница" ? "event--friday" : "" )}>
                    <header className="event__header">  
                      <h3 className="event__day">{this.eventData.day}</h3>

                      <p className="event__time">{this.eventData.time}</p>


                  </header> 
                    <p className="event__title">{this.eventData.title}</p>
                    <p className="event__about" dangerouslySetInnerHTML={this.pasteEvent()} />
                  
                </div>
    }
  }
  
  export default Event;


